import {
  FETCH_JOB_ORDER_PENDING,
  FETCH_JOB_ORDER_APPROVE,
} from "./actionTypes";

export const fetchPendingJobOrder = (pending) => {
  return {
    type: FETCH_JOB_ORDER_PENDING,
    pending,
  };
};

export const fetchApproveJobOrder = (approve) => {
  return {
    type: FETCH_JOB_ORDER_APPROVE,
    approve,
  };
};
